@use '../../stylesheets/variables' as v;
@use '../../stylesheets/basics' as b;

.about-section-container {
    position: relative;
    // border-bottom: 1px solid #EEEEEE;
    margin-top: calc(200px + (350 - 200) * ((100vw - 480px) / (1920 - 480)));
    padding-bottom: 50px;


    .about-section-content-wrapper {
        display: flex;
        flex-direction: column;
        height: 100%;
        width: 100%;
        position: relative;
        align-items: center;

        .about-section-content-container {
            max-width: 1235px;
            width: 60vw;
            height: 688px;
            display: flex;
            margin: 0 auto;
            position: relative;
            @include b.for-tablet() {
                width: 90vw;
            }
            @include b.for-phone() {
                width: 90vw;
                height: 800px;
            }
        }
    }

}