@use '../../stylesheets/basics' as b;

.navbar-container {
    position: fixed;
    height: 80px;
    background-color: rgba(255, 255, 255, .8);
    width: 100vw;
    box-shadow: 0 2px 4px -1px rgba(0,0,0,0.06), 0 4px 5px 0 rgba(0,0,0,0.06), 0 1px 10px 0 rgba(0,0,0,0.08);
    display: flex;
    align-items: center;
    z-index: 10;
    transition: all .5s ease-in-out;
    
    .navbar-content {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 100%;
        padding: 0 0 0 10vw;
    }

    .navbar-items {
        display: none;
    }
}

.navbar-active {
    height: 100vh;
    background-color: rgba(255, 255, 255, .95);

    .navbar-content {
        display: none;
    }

    .navbar-items {
        display: flex;
        flex-direction: column;
        height: 90%;
        width: 100%;
        justify-content: space-evenly;
        align-items: center; 

        @include b.for-phone() {
            height: 75%;
        }

        .navbar-items-list {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
        }
    }
}

.hiddenNav {
    transform: translateY(-100%);
}