@use '../../stylesheets/variables' as v;
@use '../../stylesheets/basics.scss' as b;

.company-name-container {
    display: flex;
    flex-direction: column;
    align-items: center;

    h1, h2 {
        font-weight: 100;
        @include b.no-text-highlight;
    }
    
    h1 {
        font-size: calc(80px + (115 - 80) * ((100vw - 480px) / (1920 - 480)));
        font-family: v.$header-font;
    }

    h2 {
        font-size: calc(28px + (40 - 28) * ((100vw - 480px) / (1920 - 480)));
        line-height: 0.5;
        
        span {
            display: inline-block;
            position: relative;
            padding: 0px 15px; 
            font-family: v.$header-font; 

            &:before, &:after {
                content: "";
                position: absolute;
                height: 5px;
                border-top: 2px solid;
                top: 5px;
                width: 45%;
            }

            &:before {
                right: 100%;
            }

            &:after {
                left: 100%;
            }
        }
    }

    h3 {
        font-size: calc(20px + (23 - 20) * ((100vw - 480px) / (1920 - 480)));
        font-family: v.$header-font;
        text-align: center;
        font-weight: 100;
        margin-top: 1.5em;
        max-width: 350px; // Arbitary value

        @include b.for-tablet() {
            font-weight: 400;
            max-width: 300px; // Arbitary value
        }
    }

}


